.txt-field-container{
    position: relative;
    /* max-width:425px;
    width:100%;
    display: flex;
    align-items: center;
    flex-basis: auto; */
}
.txt-field{
    width:100%;
    border-radius: 6px;
    margin-top:1px;
    margin-bottom:10px;
    height:8vh;
    min-height:45px;
    max-height:68px;
    border:1px solid #8692A6;
    padding:0 30px;
    font-weight: 600;
    overflow: hidden;
    position: relative;
    display: inline-block;
}
.valid-span{
    visibility: hidden;
    position: absolute;
    right:5%;
    top:40%;
    /* height: 40%; */
    font-size:20px;
}
.popup{
    position: absolute;
    /* display: inline-block; */
    width:60%;
    max-width: 200px;
    margin:5px auto;
    right: 10%;
    top:8px;
    transform: translate(30%);
    cursor: pointer;
}
.popup-txt{
    padding: 5px 10px;
    visibility:hidden;
    /* width:60%; */
    color:white;
    background-color:rgba(255, 0, 0, 0.739);
    text-align: center;
    border-radius:6px;
    padding:8px 5px;
    position: absolute;
    z-index: 1;
    bottom:50%;
}
.popup-txt::after{
    content:'';
    position:absolute;
    top:100%;
    left:50%;
    margin-left:-5px;
    border:5px solid;
    border-color: red transparent transparent transparent
}

.power-container{
    margin-bottom:5px;
    display: none;
}
.fill-container{
    position: relative;
    width:100%;
    height: 4px;
    background-color: #C4C4C4;
    border-radius: 4px;
    margin-bottom:4px;
}
.fill{
    border-radius: 4px;
    position: absolute;
    max-width:100%;
    height: 4px;
}
.checkbox{
    margin-bottom:7px;
    margin-right:11px;
}
.checkbox-txt{
    font-size:14px;
}
.popup-checkbox{
    position: relative;
    width:60%;
    max-width: 200px;
    margin:5px auto;
    right: 0%;
    transform: translate(30%);
    cursor: pointer;
}
.popup-txt-checkbox{
    padding: 5px 10px;
    visibility:hidden;
    color:white;
    background-color:rgba(255, 0, 0, 0.739);
    text-align: center;
    border-radius:6px;
    padding:8px 5px;
    position: absolute;
    z-index: 1;
    bottom:50%;
}
.popup-txt-checkbox::after{
    content:'';
    position:absolute;
    top:100%;
    left:15%;
    margin-left:-5px;
    border:5px solid;
    border-color: red transparent transparent transparent
}

